export default {
	data() {
		return {
			iconType:"",
			value:""
		};
	},
	onLoad(){
		let _this=this;
		var file;
		uni.getStorage({
			key: 'file',
			success: function (res) {
				file=res.data;
				_this.submitAjax("appmodule/appFile/lend",{ids:file.id},"post",function(res){ 
					if(res.data.state){
						_this.iconType="success";
						_this.value="《"+file.name+"》已发起借阅流程!";
					}
				})
			}
		});
	}
}
